<template>
<el-container style="height: 800px; border: 1px solid #eee">
  <el-aside width="200px" style="background-color: rgb(238, 241, 246)">
     <el-menu :default-openeds="['1', '2']">
      <el-submenu index="1">
        <template slot="title"><i class="el-icon-message"></i>写日报/周报</template>
        <el-menu-item-group>
          <el-menu-item @click.native.prevent="addreport" index="1-1">写日报</el-menu-item>
        </el-menu-item-group>
      </el-submenu>
      <el-submenu index="2">
        <template slot="title"><i class="el-icon-menu"></i>查看</template>
          <el-menu-item-group v-for="(item,itemIndex) in tableData" v-bind:key="itemIndex">
         <el-menu-item  @click.native.prevent="show(item)" :data="item">{{item}}</el-menu-item>
        </el-menu-item-group>
      </el-submenu>
    </el-menu>
  </el-aside>
  
  <el-container>
    <el-header style="text-align: right; font-size: 12px">
<el-tag style="float:left">
  <el-breadcrumb separator-class="el-icon-arrow-right" style="margin-top:8px">
  <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
  <el-breadcrumb-item>本组日报</el-breadcrumb-item>
  </el-breadcrumb>
</el-tag>
      <el-dropdown>
        <i class="el-icon-user" style="margin-right: 15px"></i>
        <el-dropdown-menu slot="dropdown">
          <el-dropdown-item>退出系统</el-dropdown-item>
          <el-dropdown-item>修改密码</el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>
    </el-header>
    
    <el-main>
      <el-table :data="daliyData">
          <el-table-column prop="createdDate" label="日期">
        </el-table-column>
        <el-table-column prop="createdPerson" label="用户名">
        </el-table-column>
        <el-table-column prop="workContent" label="工作内容" >
        </el-table-column>
        <el-table-column prop="difficulty" label="遇到的困难" >
        </el-table-column>
        <el-table-column prop="solution" label="解决方法">
        </el-table-column>
        <el-table-column prop="experience" label="心得体会">
        </el-table-column>
        <el-table-column prop="plan" label="后续计划">
        </el-table-column>
      </el-table>
    </el-main>
  </el-container>
</el-container>
</template>

<style>
  .el-header {
    background-color: #B3C0D1;
    color: #333;
    line-height: 60px;
  }
  
  .el-aside {
    color: #333;
  }
</style>

<script>
  import {getRequest} from '../utils/api'
  var tableData
  export default {
     data() {
        var _this = this;
      return {
        tableData: _this.$store.getters.meun,
        daliyData:[]
      }
    },

     mounted: function () {
      var _this = this;
      getRequest("/test/groupdailyreport").then(res=> {
            console.log(res);
        if (res.status == 200) {
          var report= res.data;
            if(report.resultData=="fail"||report.resultData=="no"){
                if(report.resultData=="no"){
                   _this.$alert('权限不足，请联系管理员！', '失败!')
                }else{ 
                    _this.$alert('未登录，请重新登录！', '失败!').then(ruleForm=>{
              _this.$router.replace({path: '/'});
             });}
            }else{
         _this.daliyData =report.resultData;
         }
        } else {
          _this.$message({type: 'error', message: '数据加载失败!'});
        }
      }, res=> {
        _this.$message({type: 'error', message: '数据加载失败!'});
      });
    },
    methods: { 
      show (index) {
          var _this = this;
      if(index=="看日报"){
         _this.$router.replace({path: '/checkreport'});
      }else if(index=="看周报"){
         _this.$router.replace({path: '/checkweekreport'});
      }else if(index=="本组日报"){
         _this.$router.replace({path: '/checkgroupreport'});
      }else if(index=="本组周报"){
        _this.$router.replace({path: '/checkgroupweekreport'});
      }else if(index=="班级日报"){
        _this.$router.replace({path: '/checkclassreport'});
      }else if(index=="班级周报"){
        _this.$router.replace({path: '/checkclassweekreport'});
      }
    },
        addreport: function (){
            var _this = this;
             _this.$router.replace({path: '/addreport'});
        }
    }
      };
</script>


