import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)


export default new Vuex.Store({
    
    state: {
      user: {
        username: window.localStorage.getItem('user' || '[]') == null ? '' : JSON.parse(window.localStorage.getItem('user' || '[]')).username
      },
      roles:{
        enable:false,//用户状态是否可用
        typeid:"0", //1->组员 2->组长 3->老师 0->管理员
        username:"" //用户名
      },
      meun:{
        1:["看日报","看周报"],
        2:["看日报","看周报","本组日报","本组周报"],
        3:["看日报","看周报","班级日报","班级周报"]
      }

    },

    getters:{
      
      meun:(state)=>{ 
        if(state.roles.typeid=="1"){return state.meun[1];}
        else if(state.roles.typeid=="2"){return state.meun[2];}
        else if(state.roles.typeid=="3"){return state.meun[3];}
        }
      
    },

    mutations: {
      login (state, user) {
        state.user = user;
        window.localStorage.setItem('user', JSON.stringify(user));
      },
      setPrint(state, payload) { //设置参数
        state.roles.enable= payload.enable;
        state.roles.typeid= payload.typeid;
        state.roles.username= payload.username;
    },
      roles:(state) => {
        console.log(state.roles);
      }
    },
    actions:{
      setPrint:(context,payload)=>{
        context.commit("setPrint",payload);
      }

    }
  })
 


