import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import Home from '@/components/Home'
import AddReport from '@/components/AddReport'
import CheckReport from '@/components/CheckReport'
import CheckWeekReport from '@/components/CheckWeekReport'
import MeunList from '@/components/MeunList'
import CheckGroupReport from '@/components/CheckGroupReport'
import CheckGroupWeekReport from '@/components/CheckGroupWeekReport'
import CheckClassReport from '@/components/CheckClassReport'
import CheckClassWeekReport from '@/components/CheckClassWeekReport'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: '',
      component: HelloWorld
    },{
      path: '/home',
      name: '',
      component: Home,
      meta: {
        requireAuth: true
      },
      hidden: true
    },{
      path: '/addreport',
      name: '',
      component: AddReport,
      meta: {
        requireAuth: true
      },
      hidden: true
    },{
      path: '/checkreport',
      name: '',
      component: CheckReport,
      meta: {
        requireAuth: true
      },
      hidden: true
    },{
      path: '/checkweekreport',
      name: '',
      component: CheckWeekReport,
      meta: {
        requireAuth: true
      },
      hidden: true
    },{
      path: '/test',
      name: '',
      component: MeunList,
      meta: {
        requireAuth: true
      },
      hidden: true
    },{
      path: '/checkgroupreport',
      name: '',
      component: CheckGroupReport,
      meta: {
        requireAuth: true
      },
      hidden: true
    },{
      path: '/checkgroupweekreport',
      name: '',
      component: CheckGroupWeekReport,
      meta: {
        requireAuth: true
      },
      hidden: true
    },{
      path: '/checkclassreport',
      name: '',
      component: CheckClassReport,
      meta: {
        requireAuth: true
      },
      hidden: true
    },{
      path: '/checkclassweekreport',
      name: '',
      component: CheckClassWeekReport,
      meta: {
        requireAuth: true
      },
      hidden: true
    }

  ]
})



