<template>
<el-container style="height: 800px; border: 1px solid #eee">
  <el-aside width="200px" style="background-color: rgb(238, 241, 246)">
 <el-menu :default-openeds="['1', '2']">
      <el-submenu index="1">
        <template slot="title"><i class="el-icon-message"></i>写日报/周报</template>
        <el-menu-item-group>
          <el-menu-item @click.native.prevent="addreport" index="1-1">写日报</el-menu-item>
        </el-menu-item-group>
      </el-submenu>
      <el-submenu index="2">
        <template slot="title"><i class="el-icon-menu"></i>查看</template>
          <el-menu-item-group v-for="(item,itemIndex) in tableData" v-bind:key="itemIndex">
         <el-menu-item  @click.native.prevent="show(item)" :data="item">{{item}}</el-menu-item>
        </el-menu-item-group>
      </el-submenu>
    </el-menu>
  </el-aside>
  
  <el-container>
    <el-header style="text-align: right; font-size: 12px">
        <el-tag style="float:left">
            <el-breadcrumb separator-class="el-icon-arrow-right" style="margin-top:8px">
  <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
  <el-breadcrumb-item>写日报</el-breadcrumb-item>
  </el-breadcrumb>
        </el-tag>
      <el-dropdown>
        <i class="el-icon-user" style="margin-right: 15px"></i>
        <el-dropdown-menu slot="dropdown">
          <el-dropdown-item>退出系统</el-dropdown-item>
          <el-dropdown-item>修改密码</el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>
    </el-header>
    
    <el-main>
      <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
  <el-form-item label="工作内容" prop="workContent" required>
    <el-input type="textarea" v-model="ruleForm.workContent"></el-input>
  </el-form-item>
  <el-form-item label="遇到的困难" prop="difficulty" required>
    <el-input type="textarea" v-model="ruleForm.difficulty"></el-input>
  </el-form-item>
  <el-form-item label="解决方案" prop="solution" required>
  <el-input type="textarea" v-model="ruleForm.solution"></el-input>
  </el-form-item>
  <el-form-item label="心得体会" prop="experience" required>
    <el-input type="textarea" v-model="ruleForm.experience"></el-input>
  </el-form-item>
  <el-form-item label="后续计划" prop="plan" required>
    <el-input type="textarea" v-model="ruleForm.plan"></el-input>
  </el-form-item>
  <el-form-item>
    <el-button type="primary" @click="submitForm('ruleForm')">立即提交</el-button>
    <el-button @click="resetForm('ruleForm')">重置</el-button>
  </el-form-item>
</el-form>
 <el-alert
    title="提示："
    type="success"
    description="星期日所填写的日报为周报。">
  </el-alert>
    </el-main>
  </el-container>
</el-container>
</template>

<style>
  .el-header {
    background-color: #B3C0D1;
    color: #333;
    line-height: 60px;
  }
  
  .el-aside {
    color: #333;
  }
</style>
<script>
 import {addRequest} from '../utils/api'
  export default {
    data() {
      var _this=this;
      return {
        ruleForm: {
          workContent: '',
          difficulty: '',
          solution: '',
          experience: '',
          plan: ''
        },
        rules: {
          workContent: [
            { required: true, message: '请填写工作内容', trigger: 'blur' }
          ],
          difficulty: [
            { required: true, message: '请填写遇到的困难', trigger: 'blur' }
          ],
          solution: [
            { required: true, message: '请填写解决方案', trigger: 'blur' }
          ],
          experience: [
            { required: true, message: '请填写心得体会', trigger: 'blur' }
          ],
          plan: [
            { required: true, message: '请填写后续计划', trigger: 'blur' }
          ]
        },
        tableData: _this.$store.getters.meun
      };
    },
    methods: {
      addreport:function(){
          var _this = this;
         _this.$router.replace({path: '/addreport'});
      }, show (index) {
          var _this = this;
      if(index=="看日报"){
         _this.$router.replace({path: '/checkreport'});
      }else if(index=="看周报"){
         _this.$router.replace({path: '/checkweekreport'});
      }else if(index=="本组日报"){
         _this.$router.replace({path: '/checkgroupreport'});
      }else if(index=="本组周报"){
        _this.$router.replace({path: '/checkgroupweekreport'});
      }else if(index=="班级日报"){
        _this.$router.replace({path: '/checkclassreport'});
      }else if(index=="班级周报"){
        _this.$router.replace({path: '/checkclassweekreport'});
      }
    },
      submitForm(formName) {
        var _this=this;
        if(this.ruleForm.workContent==''||this.ruleForm.difficulty==''||this.ruleForm.solution==''||this.ruleForm.experience==''||this.ruleForm.plan==''){
          _this.$alert('提交失败,数据不能为空，请重试!', '失败!');
        }else{
       addRequest("/test/addreport",{
         workContent: this.ruleForm.workContent,
          difficulty: this.ruleForm.difficulty,
          solution: this.ruleForm.solution,
          experience: this.ruleForm.experience,
          plan: this.ruleForm.plan
       }).then(resp=> {
          _this.loading = false;
          if (resp.status == 200) {
            //成功
            var json = resp.data;
            console.log(json.resultData);
            if (json.resultData == 'success') {
              _this.$router.replace({path: '/checkreport'});
            } else {
              _this.$alert('未登录，请重新登录！', '失败!').then(ruleForm=>{
              _this.$router.replace({path: '/'});
            });
            }
          } else {
            //失败
            _this.$alert('提交失败，请重试!', '失败!');
          }
        }, resp=> {
          _this.loading = false;
          _this.$alert('找不到服务器', '失败!');
        });
       
      }},
      resetForm(formName) {
        this.$refs[formName].resetFields();
      }
    }
  }
</script>


